* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    max-width: 100%;
    /* border: 1px solid gray; */
}

/* .container {
    width: 100% !important;
} */

body {
    background-color: rgb(241, 242, 246)
}

.inline {
    display: inline-block;
}

.navbar-toggle {
    background-color: #3BAFDA;
}

.navbar-toggle .icon-bar {
    background-color: rgb(241, 242, 246);
}

.home_image {
    background-image: url('../Images/home-pana.svg');
    background-repeat: no-repeat;
    width: 100%;
    height: 550px;
    background-position: bottom;
}


.availability_image {
    background-image: url('../Images/calendar-pana.svg');
    background-repeat: no-repeat;
    background-position: center;
    height: 480px;
    width: 50%;
    min-width: 300px;
    background-size: contain;
}



.schedule_image {
    background-image: url('../Images/whole-year-pana.svg');
    background-repeat: no-repeat;
    background-position: center;
    height: 480px;
    width: 50%;
    min-width: 300px;
    background-size: contain;
}

.shifts_image {
    background-image: url('../Images/coffee-shop-pana.svg');
    background-repeat: no-repeat;
    background-position: right bottom;
    height: 480px;
    width: 50%;
    background-size: contain;
}

@media (min-width: 1500px) {

    .shifts_image,
    .availability_image {
        max-width: 1500px;
        display: inline-block;
        margin-right: auto;
        margin-left: auto;
    }
}

@media (max-width: 767px) {
    .flex-container {
        flex-direction: column;
    }

    .schedule_image {
        display: none;
    }

    .availability_image {
        display: none;
    }

    .special {
        width: auto !important;
        position: relative;
        margin-left: 5%;
        min-width: none;
    }

    .avail-special {
        width: auto !important;
        position: relative;
        margin-left: 5%;
        min-width: none;
    }

}

.flex-container {
    display: flex;
}

.flex-right {
    justify-content: space-around;
}

.flex-child-list {
    width: 45%;
    min-width: 500px;
}

.box {
    display: inline;
    border: 1px rgb(189, 188, 188) solid;
    border-radius: 5px;
    background-color: none;
    padding-top: 10px;
    padding-bottom: 15px;
    padding-right: 10px;
    padding-left: 5px;
}

.special {
    margin-right: 5%;
    min-width: 400px;
}

.avail-special {
    margin-right: 5%;
    min-width: 450px;
}

.width-50percent {
    width: 50%;
}

.width-80percent {
    width: 80%;
}

.width-100percent {
    width: 100%;
}

.width-300 {
    width: 300px;
}

.width-200 {
    width: 200px;
}

.width-100 {
    width: 100px;
}

.width-50 {
    width: 50px;
}

.transparent {
    background-color: none;
}

.gray-text {
    color: #7a7a7a;
    font-weight: normal;
}

.blue {
    color: #3BAFDA;
}

.page {
    height: 100%;
}

.align-center {
    margin-left: auto;
    margin-right: auto;
}

/*HOME*/
.centered-button {
    position: relative;
    top: 30%;
    background-color: rgba(255, 255, 255, 0.8);
}

.little-opacity {
    opacity: 0.9;
}

/*END HOME*/

/* LOGIN PAGE */
.form-label {
    color: rgb(155, 151, 151);
    font-size: 1.4rem;
    font-weight: 300;
}

.no-bullets {
    list-style-type: none;
}

.login-panel {
    max-width: 600px;
    margin: 10vh auto;
    padding-left: 3rem !important;
    padding-right: 3rem !important;
}

.red {
    color: rgb(241, 242, 246);
    background-color: #DA4453;
}

/* END LOGIN PAGE */

/*VIEW SHIFTS PAGE*/
.top-border {
    border-top: #dbdada 2px solid;
}

.no-wrap {
    white-space: nowrap;
}

.width-33percent {
    width: 33%;
}


/*END VIEW SHIFTS PAGE*/

/* VIEW SCHEDULES PAGE */
.list-group>li {
    list-style-type: none;
}

.float-right {
    float: right;
}

.title-spacing {
    height: 64px;
}

.center {
    text-align: center !important;
}

th {
    text-align: center;
}

.parent {
    position: relative;
}

.child {
    /* ... */
    position: absolute;
    top: 0;
    right: 0;
}

.vertical-center {
    margin: 0;
    position: absolute;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

.list-subheader {
    background-color: #AAB2BD;
    text-align: center;
    padding-top: 4px;
    padding-bottom: 4px;
    color: #F5F7FA;
}

.cursor-auto {
    cursor: pointer;
    pointer-events: none;
}

.fake-disabled {
    opacity: 0.7;
    border-color: #AAB2BD;
    background-color: #E6E9ED;
    border-color: transparent;

}

/* END VIEW SCHEDULES PAGE */

/* AVAILABILITY PAGE*/

/*END AVAILABILITY PAGE*/

/* EDIT SCHEDULE */
.gray {
    background-color: #E6E9ED;
    color: #AAB2BD;
}

.hide {
    display: none;
}

.remove-btn {
    width: 20px;
    height: 20px;
    padding: 0;
    margin: 0;
}

.vertical-align {
    vertical-align: middle;
}


th h4#day {
    color: #3BAFDA;
    padding-top: 10px;
    margin-top: 0px;
    padding-bottom: 0px;
    margin-bottom: 0px;
}

th small#tabledate {
    color: #7a7a7a;
    font-weight: lighter;
}

#edit-table td {
    border-right: solid 1px #dbdbdb;
}

#time-slot {
    margin-left: 2px;
    margin-right: 2px;
    margin-bottom: 0;
    margin-top: 0;
    padding: 5px;
    border-radius: 5px;
    background-color: #AAB2BD;
    color: white;
    text-align: left;
    padding-left: 10px;
    height: 32px;
    padding-right: 2.0rem;
}

#center {
    text-align: center;
}

#back-button {
    position: relative;
    top: -10px;
    left: 10px;
    width: 60px;
    height: 60px;
}

#back-button-icon {
    font-size: 3.5em;
}

#finalize-btn {
    position: relative;
    top: 10px;
    right: 50px;
}

.invisi-btn {
    background-color: rgba(255, 255, 255, 0);
    border: 0;
    margin-top: 0.6rem;
    margin-right: 0.5rem;
}

/* END EDIT SCHEDULE */


